import { PropTypes } from 'prop-types';
import './badge.css';

export const Badge = ({ size, color, primary }) => {
    return (
        <div className='container'>
            <svg width={ size } height={ size } viewBox="0 0 16 16" xmlns="http://www.w3.org/2000/svg">
                <g fill={ color } stroke="#000000" stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5">
                    <path d="M1.75 3.75h12.5v9.5H1.75z"/>
                    <path d="m2.25 4.25l5.75 5l5.75-5"/>
                </g>
            </svg>
            <div className='count' style={{ background: primary ? 'green' : 'red' }}>{ size }</div>
        </div>
    );
};

Badge.propTypes = {
    size: PropTypes.number,
    color: PropTypes.string,
    primary: PropTypes.bool,
};

Badge.defaultProps = {
    size: 150,
    color: '#f90',
    primary: true,
}